/* Style for Header */
header {
	color: #202020;
	text-align: center;
	font-weight: bold;
}

header .title {
	margin: 30px auto 15px;
	font-size: 2rem;
}

.billboard {
	margin: 10px auto;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
}

.billboard-hidden {
	opacity: 0;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}


