/* Style for panel
-------------------- */

.panel {
  display: inline-block;
  width: 25vw;
  margin: 0 3vw;
  color: #202020;
}

.name {
  width: 20vw;
  margin: 5px auto;
  color: #797979;
  font-size: 1.5rem;
  font-weight: bold;
}

.field {
  width: 20vw;
  margin: 5px auto;
  text-align: left;
}

.tag {
  display: inline-block;
  width: 10ch;
  font-weight: bold;
  vertical-align: middle;
}

.input {
  display: inline-block;
  vertical-align: middle;
  border-right: 0.1em solid transparent;
  white-space: nowrap;
  overflow: hidden;
  -webkit-animation: typing 1s steps(5, end), caret 0.2s step-end 5 alternate;
          animation: typing 1s steps(5, end), caret 0.2s step-end 5 alternate;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}

@-webkit-keyframes caret {
  50% {
    border-color: #111;
  }
}

@keyframes caret {
  50% {
    border-color: #111;
  }
}



